.rightmsg{
    display: flex;
    justify-content: space-between;
    margin: 20px 5px;
    border-bottom: 1px solid #000;
    padding-bottom: 10px;
}

.messageContent{
    display: flex;
    justify-content: start;
    align-items: center;
}

.messageHeading{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    color: #fff;
    transition: .3s;
}

.messageHeading:hover{
    text-decoration: underline;
    color: #aba9a9;
}

.messageHeading span:nth-child(2){
    opacity: 0.7;
    font-size: 12px;
}

.messageRadius{
    margin-right: 15px;
}

.messageRadius img{
    width: 50px;
    height: 50px;
    border-radius: 50%;
}

.messageDate span{
    opacity: 0.8;
    font-size: 12px;
}

.messageDate span:first-child{
    margin-right: 5px;
}