.conversationWrapper{
    height: 100%;
    width: 100%;
    background-color: #1a1a1a;
    padding: 15px 30px;
}

.imgWrapper{
    margin-right: 15px;
}

.imgWrapper img{
    width: 50px;
    height: 50px;
    border-radius: 50%;
}

.messageBox{
    display: flex;
    align-items: center;
    margin: 20px 5px;
    max-height: 400px;
}

.message{
    width: 60%;
}

.message p{
    background-color: #fa2276;
    color: #ffffff;
    width: auto;
    padding: 20px 25px;
    float: left;
    box-shadow: 0px 1px 4px 1px #333;
    border-top-right-radius: 50px;
    border-bottom-right-radius: 50px;
    border-top-left-radius: 50px;
    border-bottom-left-radius: 0px;
    font-size: 13px;
    width: 60%;
}

.messageBoxWrapper{
    max-height: 400px;
    overflow-y: scroll;
}

.messageField{
    display: flex;
}

.messageField textarea{
    width: 84%;
    background-color: #1b1b1b;
    color: #bdc3d3;
    font-size: 14px;
    padding: 0 15px;
    border-top: 0px;
    border-left: 0px;
    border-radius: 0;
    border-right: 0px;
    border-bottom: 1px solid #bdc3d3;
    height: 45px;
    outline: none;
} 



.messageField button{
    width: 13%;
    background-color: #fa2276;
    height: 45px;
    text-align: center;
    color: #fff;
    font-weight: 400;
    border: 0;
    font-size: 14px;
    text-transform: capitalize;
    margin-left: 15px;
}


.messageSendArea{
    border-top: 1px solid #000;
    padding: 20px 0;
}

.messageField input{
    width: 84%;
    background-color: #1b1b1b;
    color: #bdc3d3;
    font-size: 14px;
    padding: 0 15px;
    border-top: 0px;
    border-left: 0px;
    border-radius: 0;
    border-right: 0px;
    border-bottom: 1px solid #bdc3d3;
    height: 45px;
    outline: none;
} 



